export class WorkerEvent extends CustomEvent {
    constructor (_self, _msg) {
        super(_msg.data.msg, {
            detail: _msg.data.param
        });
        Object.defineProperties(this, {
            ports: {
                value: _msg.ports,
                writable: false
            },
            oriMsg: {
                value: _msg,
                writable: false
            },
            worker: {
                value: _self,
                writable: false
            },
            msgType: {
                value: _msg.data.msg,
                writable: false
            },
            timeStamp: {
                value: Date.now(),
                writable: false
            }
        });
    }

    broadcasrAck(_msg) {
        const data = {
            msg: this.msgType,
            timeStamp: this.timeStamp,
            ret: _msg
        };
        this.worker.clients.matchAll().then(function (clients) {
            clients.forEach(function (client) {
                client.postMessage(data);
            });
        });
    }

    ack(_msg) {
        if (this.ports[0]) {
            this.ports[0].postMessage({
                msg: this.msgType,
                timeStamp: this.timeStamp,
                ret: _msg
            });
        } else {
            this.broadcasrAck(_msg);
        }
    }
}
